import React from 'react';
import './App.css';
import Order from './components/Order/Order'



class App extends React.Component {
    constructor() {
        super();
        this.state = {
            startScreen: false
        }
    }

    startHandler = () => {

        this.setState({
            startScreen: !this.startScreen
        })
    }

    render() {
        return (
            <div className="App">
                <div>
               
                    {this.state.startScreen === false && 
                      <header className="App-header">
                        <h1>SEO Content Orderer!</h1>
                        
                        <button onClick={this.startHandler} className="btn btn-light">Press to Start!</button>
                    </header>
                  }
                    {this.state.startScreen !== false && 
                      <Order/>
                    }

                </div>
            </div>
        );
    }

}

export default App;
