import React, {Component} from 'react';

class LpInputOrder extends Component {
    constructor(props) {
        super(props);
        this.state = {
            allLandings: [],
            landingPage : {
                lpUrl: '',
                phrases: '',
                content: '',
                signs: ''
            },
            rdySubmit: false,

        };
    }

    render() {
        return (
            <React.Fragment>
                <h3 className="my-5">Specific Landing Pages Data</h3>
                <form onSubmit={this.props.submitLpData} className="w-75 mx-auto">
                <div className="form-group row">
                    <label className="col-sm-3 col-form-label" htmlFor="lpUrl">Landing Page URL:</label>
                    <div className="col-sm-9">
                        <input
                            type="text"
                            className="form-control"
                            id="lpUrl"
                            onChange={this.props.handleChange}
                            name="lpUrl"
                            value={this.props.lpUrl}
                            placeholder="e.g. https://xyz.com/wedding-parties-poznan/"
                            required
                            />
                    </div>
                </div>
                <div className="form-group row">
                    <label className="col-sm-3 col-form-label" htmlFor="phrases">Phrases for Landing Page</label>
                    <div className="col-sm-9">
                        <input
                            type="text"
                            className="form-control"
                            id="phrases"
                            onChange={this.props.handleChange}
                            name="phrases"
                            value={this.props.phrases}
                            placeholder="e.g. wedding parties poznan, bridal parties poznan, cheap wedding party poznan, best bridal party poznan"
                            required
                            />
                    </div>
                </div>
                <div className="form-group">
                    <label htmlFor="content">Heading, paragraphs, sections</label>
                    <textarea
                        placeholder="H1: Wedding parties Poznan
Generally about this type of parties, about companies at Poznan etc
1000 signs
H2: Why you should choose wedding parties in Poznan with Johns Weddings?
Client advantages, experience, positive reviews, about organization
1000-1500 signs "
                        className="form-control"
                        id="content"
                        onChange={this.props.handleChange}
                        name="content"
                        value={this.props.content}
                        rows="6"
                        required
                        ></textarea>
                </div>
                <div className="form-group row">
                    <label className="col-sm-3 col-form-label" htmlFor="signs">Approximately number of signs</label>
                    <div className="col-sm-9">
                        <input
                            type="text"
                            className="form-control"
                            id="signs"
                            onChange={this.props.handleChange}
                            name="signs"
                            value={this.props.signs}
                            placeholder="e.g. 1500-2000"
                            required
                            />
                    </div>
                </div>
                <div className="form-group row">
                        <div className="col-sm-12">
                            <button type="submit" className="btn btn-primary">Save LP Data</button>
                            {this.props.allLandings >= 1 ? <button onClick={this.props.finalizeOrder} type="button" className="btn btn-danger mx-2">Finish Order!</button> : null}
                        </div>
                    </div>
                </form>
                
            </React.Fragment>
        );
    }
}

export default LpInputOrder;