import React, {Component} from 'react';
import styles from "./Order.module.css";
import InputOrder from '../InputOrder/InputOrder'
import LpInputOrder from '../InputOrder/LpInputOrder';

class Order extends Component {
    constructor(props) {
        super(props);
        this.state = {
            rdySubmit: false,
            rdySubmitMain: false,
            rdySubmitLps: false,
            clientData: {
                domainUrl: '',
                industry: '',
                isNew: ''
            },
            allLandings: [],
            landingPage: {
                lpUrl: '',
                phrases: '',
                content: '',
                signs: ''
            },
            disabled: false
        }

    };

    // INPUT ORDER START

    submitDomainData = (event) => {
        event.preventDefault();
        this.setState({
            disabled: !this.state.disabled,
            rdySubmitMain: true
        })
    }

    handleChangeClient = (event) => {

        let newDomainData;

        if (event.target.name === 'isNew') {

            newDomainData = {
                ...this.state.clientData,
                [event.target.name]: event.target.checked
            }

        } else {

            newDomainData = {
                ...this.state.clientData,
                [event.target.name]: event.target.value
            }

        }

        this.setState({clientData: newDomainData})

    }

    // INPUT ORDER END LP-INPUT ORDER START

    submitLpData = (event) => {

        event.preventDefault();

        let newLanding = {
            ...this.state.landingPage
        };

        this.setState({
            allLandings: [
                ...this.state.allLandings,
                newLanding
            ],
            landingPage: {
                lpUrl: '',
                phrases: '',
                content: '',
                signs: ''
            },
            rdySubmitLps: true
        })

    }

    handleChange = (event) => {

        let newLanding;

        newLanding = {
            ...this.state.landingPage,
            [event.target.name]: event.target.value
        };

        this.setState({landingPage: newLanding})

    }

    // LP-INPUT ORDER END FINALIZE ORDER

    finalizeOrder = () => {
        this.setState({
            rdySubmit: !this.rdySubmit
        })
    }

    // MAPPING OF LPurls
    


    render() {

        let urlsMap = this.state.allLandings.map((url,i) => 
            <div key={i} className="row mt-5 text-left">
                        <div className={`${styles.cardLp} card w-75 mx-auto`}>
                            <div className="card-body">
                                <p className="card-title"><span className="font-weight-bold">LP URL:</span> <p>{url.lpUrl}</p></p>
                                <p className="card-text"><span className="font-weight-bold">Phrases for Landing Page:</span> <p>{url.phrases}</p></p>
                                <p className="card-text"><span className="font-weight-bold">Heading, paragraphs, sections:</span> <p>{url.content}</p></p>
                                <p className="card-text"><span className="font-weight-bold">Approximately number of signs:</span> <p>{url.signs}</p></p>
                            </div>
                        </div>
                    </div>
        );


        return (
            <div>
                {!this.state.rdySubmit && <div className={`${styles.orderMain}`}>

                    <h1 className="py-5 m-0">Start making your order!</h1>
                    <InputOrder
                        disabled={this.state.disabled}
                        submitDomainData={this.submitDomainData}
                        handleChangeClient={this.handleChangeClient}
                        domainUrl={this.state.clientData.domainUrl}
                        industry={this.state.clientData.industry}
                        isNew={this.state.clientData.isNew}/>
                    <LpInputOrder
                        submitLpData={this.submitLpData}
                        handleChange={this.handleChange}
                        lpUrl={this.state.landingPage.lpUrl}
                        phrases={this.state.landingPage.phrases}
                        content={this.state.landingPage.content}
                        signs={this.state.landingPage.signs}
                        allLandings={this.state.allLandings.length}
                        finalizeOrder={this.finalizeOrder}/>

                </div>}
                {this.state.rdySubmit && <div className={`${styles.finalOrder}`}>
                    <h1 className="py-5 m-0">FINALIZED ORDER</h1>
                    <div className="row">
                        <div className={`${styles.cardDomain} card w-75 mx-auto`}>
                            <div className="card-body">
                                <p className="card-title"><span className="font-weight-bold">Domain URL:</span> {this.state.clientData.domainUrl}</p>
                                <p className="card-text"><span className="font-weight-bold">Type of Industry:</span> {this.state.clientData.industry}</p>
                                <p className="card-text"><span className="font-weight-bold">New Client:</span> {this.state.clientData.isNew ? 'Yes' : 'No'}</p>
                            </div>
                        </div>
                    </div>
                    {urlsMap}
                </div>
               
}
            </div>

        );
    }
}

export default Order;